.grid > [class*=span-sm],
.grid > [class*=span-md],
.grid > [class*=span-lg],
.grid > [class*=span-xl],
.grid > [class*=span-xxl],
.grid > [class*=span-] {
  grid-column: 1/-1;
}

.auto-grid {
  display: grid;
  gap: var(--gap, 1rem);
  grid-template-columns: repeat(var(--grid-type, auto-fit), minmax(min(180px, 100%), 1fr));
